import React from 'react';
import './App.css';

import adjectives from './data/adjectives';
import nouns from './data/nouns';
import separators from './data/separators';

/**
 * Generate an elite hacker name.
 */
const generateHackerName = () => {
  const adjective  = adjectives[Math.floor(Math.random() * adjectives.length)];
  const noun       = nouns[Math.floor(Math.random() * nouns.length)];
  const separator  = separators[Math.floor(Math.random() * separators.length)];
  let hackerName = `${adjective}${separator}${noun}`;
  if (/\s/.test(hackerName) && /_/.test(hackerName)) {
    hackerName = hackerName.replace(new RegExp(' ', 'g'), '_'); // Replace spaces with underscore
  }
  return hackerName;
}

const App = () => {
  return (
    <div className="App">
      <header className="App-header">
        <p>
          &gt; your new hacker name is...
        </p>
        <p className="Text-hacker-name">
          {generateHackerName()}
        </p>
        <p className="Text-source-code">
          <a href="https://github.com/shadforth/hacker-name-generator">Source code on GitHub</a>
        </p>
      </header>
    </div>
  );
}

export default App;
